/**
 * 🎯 퀴즈 문제 화면 컴포넌트
 * 
 * 📋 주요 기능:
 * - 문제 표시 (마크다운 형식 지원)
 * - 선택지 표시 및 선택 처리
 * - 답안 제출 및 채점 결과 표시
 * - 힌트 및 학습 모드 제공
 * - 진행도 관리 및 네비게이션
 * 
 * 🔗 백엔드 연동:
 * - getQuestions(): 레벨 기반 퀴즈(문항 목록) 조회 (levels/{levelId}/quizzes -> quizzes/{quizId})
 * - submitAnswer(): 답안 제출 및 채점
 */
import React, { useState, useRef, useEffect } from "react";
import "./QuizQuestion.css";

import ProgressHeader from "./ProgressHeader";
import q4ArticlePng from "../../assets/explore/q4-article.png";
// getKeyPoints 제거: 문제 객체에 포함된 solvingKeypointsMd / teachingExplainerMd 사용

/**
 * 🎯 QuizQuestion 컴포넌트
 * 
 * @param {number} current - 현재 문제 번호 (0부터 시작)
 * @param {function} setCurrent - 문제 번호 변경 함수
 * @param {Array} questions - 전체 문제 배열
 * @param {number} selected - 현재 선택된 답안 인덱스
 * @param {boolean} showResult - 채점 결과 표시 여부
 * @param {function} onSelect - 답안 선택 시 호출되는 함수
 * @param {function} onCheck - 답안 확인 버튼 클릭 시 호출되는 함수
 * @param {function} onComplete - 퀴즈 완료 시 호출되는 함수
 * @param {function} onBack - 뒤로가기 버튼 클릭 시 호출되는 함수
 */
export default function QuizQuestion({ current,
  setCurrent,
  questions,
  selected,
  showResult,
  onSelect,
  onCheck,
  onComplete,
  onBack,
  answerResult,
  quizCompletionArr = [] }) {
  
  // 🎓 학습 모드 관련 상태
  const [showLearning, setShowLearning] = useState(false);        // 학습 모드 표시 여부
  const [showHint, setShowHint] = useState(false);               // 힌트 표시 여부
  const [learningText, setLearningText] = useState("");          // 학습 모드 텍스트

  // 간단 텍스트 정규화: '/n' -> 줄바꿈, CRLF 정규화
  const normalizePlain = (text) => {
    if (!text || typeof text !== 'string') return '';
    let t = text.replace(/\s*\/n\s*/g, '\n');
    t = t.replace(/\r\n?/g, '\n');
    return t;
  };


  // 📚 문제 데이터 처리
  const questionList = questions && questions.length > 0 ? questions : [];
  const question = questionList[current];
  try { console.log('Rendering Question:', question); } catch (_) {}
  // 기사형 판별(호환): 백엔드에서 'ARTICLE' 등으로 내려오거나 정규화된 'articleImage' 모두 지원
  const isArticleType = String(question?.type || '').toLowerCase() === 'articleimage' || String(question?.type || '').toLowerCase() === 'article';
  // 스토리형 판별: 정규화된 'story' 또는 스토리 본문/제목 존재 시
  const isStoryType = String(question?.type || '').toLowerCase() === 'story' || !!(question?.storyTitleMd || question?.storyBodyMd);
  
  // ✅ 정답 인덱스 계산 (옵션 isCorrect 우선, 없으면 correctOptionId로 폴백 - 숫자/문자 모두 허용)
  let correctIdx = -1;
  if (Array.isArray(question?.options)) {
    const byFlag = question.options.findIndex(o => o && o.isCorrect === true);
    if (byFlag >= 0) correctIdx = byFlag;
    else if (question?.correctOptionId != null) {
      // 1) 문자열/숫자 동일성 비교
      const cidStr = String(question.correctOptionId);
      const byStr = question.options.findIndex(o => String(o?.id) === cidStr);
      if (byStr >= 0) correctIdx = byStr;
      else if (Number.isFinite(Number(cidStr))) {
        // 2) 최후: 숫자로도 비교
        const cidNum = Number(cidStr);
        const byNum = question.options.findIndex(o => Number(o?.id) === cidNum);
        if (byNum >= 0) correctIdx = byNum;
      }
    }
  }
  
  // 🎨 UI 관련 참조 및 상태
  const chalkTextRef = useRef(null);
  const [, setChalkLayout] = useState(null); // keep setter for resets; omit state to avoid unused var warning
  
  /**
   * 📝 학습 텍스트 정규화 함수
   * 
   * 🔧 처리 내용:
   * - Windows CRLF를 LF로 변환
   * - 유니코드 라인 구분자 정규화
   * - 과도한 연속 공백 줄 축소
   * 
   * @param {string} text - 정규화할 텍스트
   * @returns {string} - 정규화된 텍스트
   */


  /**
   * 🎨 학습 콘텐츠 렌더링 함수
   * 
   * 📋 처리 과정:
   * 1. 고정 헤더 텍스트 추가
   * 2. 서버에서 받은 학습 내용 처리
   * 3. 칠판 스타일로 텍스트 포맷팅
   * 
   * @returns {JSX.Element} - 렌더링된 학습 콘텐츠
   */
  const renderLearningContent = () => {
    try {
      // 🎯 고정 헤더 텍스트
      const headerText = "🔍 먼저 핵심개념부터 잡고 갑시다.\n\n";
      
      // 📚 서버에서 받은 학습 내용 처리
      let mainContent = "";
      if (learningText && typeof learningText === 'string') {
        mainContent = learningText;
      } else if (learningText && Array.isArray(learningText)) {
        // 📚 배열 형태의 학습 내용을 문자열로 변환
        mainContent = learningText.join('\n');
      } else {
        // ⚠️ 백엔드에서 키포인트가 없을 경우 기본 메시지
        mainContent = "서버에서 학습 내용을 불러오는 중입니다...";
      }
      // 🔤 특수 마커 처리
      // - '/n' 문자열은 줄바꿈으로 변환
      // - 마크다운 유사 굵게(**텍스트**)와 헤더(##, ###)는 시각적으로만 반영하고 마커는 숨김
      const normalizeMarkers = (text) => {
        if (!text || typeof text !== 'string') return '';
        let t = text;
        // '/n' -> 실제 개행
        t = t.replace(/\s*\/n\s*/g, '\n');
        // 윈도우 개행 정규화
        t = t.replace(/\r\n?/g, '\n');
        return t;
      };
      const stripMdMarkers = (line) => {
        // 헤더 마커 제거 후 라벨링은 렌더 단계에서 처리
        return line
          .replace(/^\s*#{2,3}\s*/, '')
          .replace(/\*\*(.*?)\*\*/g, '$1');
      };
      
      // 📝 전체 텍스트 합치기 (헤더 + 메인 콘텐츠)
      const fullText = headerText + normalizeMarkers(mainContent);
      
      // 🔄 줄바꿈별로 분리하여 각 줄을 개별 처리
      const lines = fullText.split('\n');
      
      return lines.map((line, index) => {
        const raw = line ?? '';
        const trimmed = raw.trim();
        const isH3 = /^\s*###\s*/.test(raw);
        const isH2 = /^\s*##\s*/.test(raw) && !isH3;
        const hasBold = /\*\*(.*?)\*\*/.test(raw);
        
        // 📏 빈 줄 처리 - 적절한 간격 제공
        if (!trimmed) {
          return <div key={`empty-${index}`} style={{ height: '12px' }} />;
        }
        
        // 🎯 헤더 줄 (🔍로 시작하는 첫 줄) - 굵게 강조
        if (trimmed.startsWith('🔍')) {
          return (
            <div key={`header-${index}`} style={{ 
              fontWeight: 'bold', 
              fontSize: '16px', 
              marginBottom: '16px',
              color: '#FFFFFF'
            }}>
              {trimmed}
            </div>
          );
        }
        
        // • 불릿 포인트 (•로 시작) - 목록 형태로 표시
        if (trimmed.startsWith('•')) {
          return (
            <div key={`bullet-${index}`} style={{ 
              marginBottom: '12px',
              lineHeight: '1.5',
              paddingLeft: '8px',
              color: '#FFFFFF'
            }}>
              {stripMdMarkers(trimmed)}
            </div>
          );
        }
        
        // ➡️ 화살표 설명 (→로 시작) - 부연 설명 스타일
        if (trimmed.startsWith('→')) {
          return (
            <div key={`arrow-${index}`} style={{ 
              marginBottom: '12px',
              marginLeft: '16px',
              lineHeight: '1.5',
              color: '#E6F0FF'
            }}>
              {stripMdMarkers(trimmed)}
            </div>
          );
        }
        
        // 인용문 ("로 시작)
        if (trimmed.startsWith('"')) {
          return (
            <div key={`quote-${index}`} style={{ 
              marginBottom: '8px',
              marginLeft: '20px',
              fontStyle: 'italic',
              color: '#B3D9FF',
              lineHeight: '1.4'
            }}>
              {stripMdMarkers(trimmed)}
            </div>
          );
        }
        
        // ###, ## 헤더는 굵게/큰 글씨로 강조 (마커는 숨김)
        if (isH3 || isH2) {
          return (
            <div key={`hdr-${index}`} style={{
              fontWeight: 'bold',
              fontSize: isH3 ? '17px' : '16px',
              marginTop: '8px',
              marginBottom: '12px',
              color: '#FFFFFF'
            }}>
              {stripMdMarkers(raw)}
            </div>
          );
        }

        // **굵게** 마커는 제거하고 굵게 표시
        if (hasBold) {
          const parts = raw.split(/(\*\*.*?\*\*)/g);
          return (
            <div key={`bold-${index}`} style={{ 
              marginBottom: '8px',
              lineHeight: '1.5',
              color: '#FFFFFF'
            }}>
              {parts.map((p, i) => {
                const m = /^\*\*(.*?)\*\*$/.exec(p);
                if (m) return <strong key={i}>{m[1]}</strong>;
                return <span key={i}>{p}</span>;
              })}
            </div>
          );
        }

        // 일반 텍스트
        return (
          <div key={`text-${index}`} style={{ 
            marginBottom: '8px',
            lineHeight: '1.5',
            color: '#FFFFFF'
          }}>
            {stripMdMarkers(trimmed)}
          </div>
        );
      });
    } catch (error) {
      console.error('renderLearningContent error:', error);
      return <div>학습 내용을 불러올 수 없습니다.</div>;
    }
  };

  // ===== 기사형 문제 전용 상태 =====
  // 기사 이미지: 너비는 플레이스홀더와 동일(래퍼 100%), 높이는 이미지 비율에 맞춤
  const ARTICLE_IMG_MIN = 0; 
  const [articleImgHeight, setArticleImgHeight] = useState(null);
  // 🖼️ 기사 이미지 관련 상태 및 참조
  const articleImgWrapperRef = useRef(null);
  const naturalSizeRef = useRef({ w: null, h: null });
  const [imgError, setImgError] = useState(false);
  const [imgSrc, setImgSrc] = useState(null);

  // NEW: 더미 이미지 비율을 미리 측정해 로딩 전에도 동일 규격 유지
  const defaultRatioRef = useRef(9 / 16); // fallback ratio (dummy uses 16:9 width:height)
  const [fallbackHeight, setFallbackHeight] = useState(null);
  useEffect(() => {
    // 더미 이미지 실제 비율 측정
    const probe = new Image();
    probe.onload = () => {
      if (probe.naturalWidth && probe.naturalHeight) {
        defaultRatioRef.current = probe.naturalHeight / probe.naturalWidth;
        // 래퍼 폭 기준으로 placeholder 높이 계산
        if (articleImgWrapperRef.current) {
          const wrapW = articleImgWrapperRef.current.clientWidth;
          setFallbackHeight(Math.round(wrapW * defaultRatioRef.current));
        }
      }
    };
    probe.src = q4ArticlePng;
  }, []);

  // 이미지 후보 목록: 서버 제공 필드 + 기사 객체 + 프로젝트 내 더미 이미지
  const imgCandidates = React.useMemo(() => {
    const list = [];
    const norm = (v) => (typeof v === 'string' ? v.trim() : '');
    const pushIf = (v) => {
      const s = norm(v);
      if (!s) return;
      const isHttp = /^(https?:\/\/|data:|blob:)/i.test(s);
      const isRel = /^(\.\/|\.\.\/)/.test(s);
      const isRoot = /^\//.test(s);
      // 앱 루트 /assets/... 경로는 정적 배포 경로와 불일치할 수 있어 제외
      const looksBroken = isRoot && /\/assets\//i.test(s);
      if (isHttp || isRel || (isRoot && !looksBroken)) list.push(s);
    };
    // 다양한 키 지원
  // 우선순위: 백엔드 병합 필드 우선
  pushIf(question?.imageUrl);
  pushIf(question?.articleImageUrl);
  pushIf(question?.image);
    pushIf(question?.article?.image_url);
    pushIf(question?.article?.imageUrl);
  // 마지막에 더미 이미지 폴백은 후보가 전혀 없을 때만 사용
  if (list.length === 0) list.push(q4ArticlePng);
    const result = Array.from(new Set(list.filter(Boolean)));
    try {
      console.log('[Quiz UI][Image] candidates built', {
        questionId: question?.id ?? question?.questionId ?? question?.qid,
        type: question?.type,
        from: {
          image: question?.image,
          imageUrl: question?.imageUrl,
          articleImageUrl: question?.articleImageUrl,
          article_image_url: question?.article?.image_url,
          article_imageUrl: question?.article?.imageUrl,
        },
        candidates: result,
      });
    } catch (_) {}
    return result;
  }, [question]);

  const q4FallbackIndexRef = useRef(0);

  /**
   * 🖼️ 기사 이미지 로드 완료 처리 함수
   * 
   * 📐 처리 내용:
   * - 이미지 원본 크기 저장
   * - 컨테이너 너비에 맞춰 비율 계산
   * - 최소/최대 높이 제한 적용
   * 
   * @param {Event} e - 이미지 로드 이벤트
   */
  const handleArticleImgLoad = (e) => {
    try {
      const nw = e.target.naturalWidth || 0;
      const nh = e.target.naturalHeight || 0;
      naturalSizeRef.current = { w: nw, h: nh };
      try {
        console.log('[Quiz UI][Image] onLoad', {
          src: e?.target?.src,
          naturalWidth: nw,
          naturalHeight: nh,
        });
      } catch (_) {}
      if (!articleImgWrapperRef.current || !nw || !nh) return;
      const wrapW = articleImgWrapperRef.current.clientWidth; 
      const scaledH = nh * (wrapW / nw);
      const exactH = Math.round(scaledH);
      setArticleImgHeight(exactH);
    } catch (err) {
      console.warn('이미지 크기 계산 실패:', err);
      setArticleImgHeight(ARTICLE_IMG_MIN);
    }
  };

  // 이미지 로드 에러 시 다음 후보로 전환 (inline handler에서 동일 로직 사용)

  /**
   * 📱 창 크기 변경 시 이미지 높이 재계산 (반응형 처리)
   */
  useEffect(() => {
    const onResize = () => {
      const wrap = articleImgWrapperRef.current;
      if (!wrap) return;
      const wrapW = wrap.clientWidth;
      const { w, h } = naturalSizeRef.current;
      if (w && h) {
        const scaledH = h * (wrapW / w);
        setArticleImgHeight(Math.round(scaledH));
      } else if (defaultRatioRef.current) {
        setFallbackHeight(Math.round(wrapW * defaultRatioRef.current));
      }
    };
    window.addEventListener('resize', onResize);
    return () => window.removeEventListener('resize', onResize);
  }, []);

  /**
   * 🔄 문제 변경 시 UI 상태 초기화 useEffect
   * 
   * 📋 초기화 항목:
   * - 학습 모드 상태
   * - 힌트 표시 상태
   * - 칠판 레이아웃
   * - 이미지 관련 상태
   * - 학습 텍스트 및 로딩 상태
   */
  useEffect(() => {
    // 🔄 UI 상태 전체 초기화
    setShowLearning(false);
    setShowHint(false);
    setChalkLayout(null);
    setImgError(false);
    setLearningText("");
    // 기사 이미지: 후보군 순차 시도
    if (isArticleType) {
      q4FallbackIndexRef.current = 0;
      // 초기에는 백엔드 후보가 있으면 그것만 세팅, 더미는 즉시 선택하지 않음
      const first = imgCandidates[0] || null;
      // 만약 first가 프로젝트 더미라면(null 유지) → 로딩 중 플레이스홀더 유지, onError에서만 더미로 이동
      const isDummy = first === q4ArticlePng;
      setImgSrc(isDummy ? null : first);
      try {
        console.log('[Quiz UI][Image] init for article', {
          isArticleType,
          firstCandidate: imgCandidates[0] || null,
          candidates: imgCandidates,
        });
      } catch (_) {}
      // 플레이스홀더 높이 계산 (실제 이미지 로드 전 기본 비율로 계산)
      if (articleImgWrapperRef.current && defaultRatioRef.current) {
        const wrapW = articleImgWrapperRef.current.clientWidth;
        setFallbackHeight(Math.round(wrapW * defaultRatioRef.current));
      }
    } else {
      setImgSrc(null);
    }
  }, [current, imgCandidates, question?.type, isArticleType]);

  // 보조 로그: 문제 내 기사 관련 필드 요약
  useEffect(() => {
    if (!question) return;
    if (!isArticleType) return;
    const art = question?.article;
    const artSummary = art && typeof art === 'object'
      ? {
          keys: Object.keys(art || {}),
          image_url: art?.image_url,
          imageUrl: art?.imageUrl,
          title: art?.title || art?.titleMd || null,
        }
      : null;
    try {
      console.log('[Quiz UI][Image] article fields summary', {
        questionId: question?.id ?? question?.questionId ?? question?.qid,
        isArticleType,
        image: question?.image,
        imageUrl: question?.imageUrl,
        articleImageUrl: question?.articleImageUrl,
        articleTitleMd: question?.articleTitleMd,
        articleBodyMd: (question?.articleBodyMd || '').slice(0, 80) + (question?.articleBodyMd && question.articleBodyMd.length > 80 ? '…' : ''),
        article: artSummary,
      });
    } catch (_) {}
  }, [question, isArticleType]);

  // 학습하기가 열릴 때 백엔드에서 받은 퀴즈 데이터의 키포인트를 사용
  useEffect(() => {
    const q = question;
    if (!showLearning || !q) return;
    
    try {
      // 백엔드에서 받은 퀴즈 데이터에서 학습(칠판) 내용 추출
      // 요구사항: teachingExplainerMd가 칠판에 들어감
      const pickLocalLearning = () => {
        if (q.teachingExplainerMd) return q.teachingExplainerMd;         // 1순위: 학습 설명(칠판)
        if (q.solvingKeypointsMd) return q.solvingKeypointsMd;           // 2순위: 핵심 포인트(폴백)
        if (q.hintMd) return q.hintMd;                                   // 3순위: 힌트(최후 폴백)
        return null;
      };

      const localText = pickLocalLearning();
      if (localText) {
        setLearningText(localText);
        return;
      }

      // 추가 API 호출을 제거하고 로컬 필드만 사용
      setLearningText("이 문제에 대한 학습 내용을 불러오지 못했습니다.");
    } catch (e) {
      // 🚨 학습 내용 로드 실패 처리
      console.error('학습 내용 로드 실패:', e);
    }
  }, [showLearning, question, current]); // 학습 모드 토글 또는 문제 변경 시 실행

  /**
   * 📱 칠판 레이아웃 반응형 계산 useEffect
   * 
   * 📐 계산 항목:
   * - 텍스트 영역 높이
   * - 칠판 전체 높이
   * - 분필 라인 위치
   * - 칠판 배경 영역 크기
   */
  // 칠판 레이아웃은 CSS가 자동으로 처리하도록 변경 (측정 로직 제거)

  /**
   * 🎯 선택지 선택 처리 함수
   * 
   * 📋 처리 과정:
   * - 결과 표시 상태에서는 클릭 차단
   * - 정상 상태에서 선택지 인덱스 전달
   * 
   * @param {number} index - 선택된 선택지 인덱스
   */
  const handleSelect = (index) => {
    if (showResult) return;   // 채점 이후엔 클릭 못하게
    onSelect(index); 
  };

  /**
   * ➡️ 다음 문제로 이동 처리 함수
   * 
   * 📋 처리 과정:
   * - 문제 목록 내 다음 문제 확인
   * - 다음 문제가 있으면 current 증가
   * - 마지막 문제면 완료 처리
   */
  const handleNext = () => {
    if (current + 1 < questionList.length) {
      setCurrent(current + 1);
    } else {
      onComplete();
    }
  };

  const containerRef = useRef(null);
  // 🔘 버튼 영역 관련 참조 및 상태
  const buttonRef = useRef(null);
  const [bottomPad, setBottomPad] = useState(200); // 초기 값 (첫 렌더 보호)

  /**
   * 📏 버튼 높이 기반 안전 패딩 동적 계산 useEffect
   * 
   * 📋 처리 내용:
   * - 하단 버튼 영역 높이 측정
   * - 콘텐츠와 버튼 간 안전 여백 계산
   * - 화면 크기 변경 시 재계산
   */
  useEffect(() => {
    const calc = () => {
      // ⚠️ 필요한 참조가 없으면 종료
      if (!containerRef.current || !buttonRef.current) return;
      
      // 📏 버튼 영역 실제 높이 측정
      const btnRect = buttonRef.current.getBoundingClientRect();
      const height = btnRect.height;
      const extra = 24; // 추가 여백
      
      // 💾 계산된 패딩 값 저장
      setBottomPad(height + extra);
    };
    
    // 🔄 초기 계산 및 리사이즈 이벤트 등록
    calc();
    window.addEventListener("resize", calc);
    return () => window.removeEventListener("resize", calc);
  }, [showResult, selected]); // 결과 표시 상태나 선택 상태 변경 시 재계산

  /**
   * 📐 칠판 높이 개선된 계산 useEffect
   * 
   * 🔧 개선사항:
   * - 텍스트 렌더링 완료 후 높이 측정
   * - 최소 높이 보장
   * - 동적 여백 계산
   */
  // 높이 강제 보정 로직 제거: CSS와 scrollHeight 기반 측정으로 자연 확장

  /**
   * ⚠️ 문제 데이터 없음 상태 처리
   * 
   * 📋 표시 내용:
   * - 로딩 메시지
   * - 백엔드 연결 확인 안내
   * - 뒤로가기 버튼
   */
  if (!question) {
    return (
      <div className="quiz-question-header" style={{ textAlign: "center" }}>
        <h2>문제를 불러오는 중입니다...</h2>
        <p>백엔드 서버와 연결을 확인해주세요.</p>
        <button onClick={onBack} className="quiz-question-bottom-btn" style={{ width: "auto", padding: "10px 20px", borderRadius: 5, background: "#448FFF" }}>
          뒤로 가기
        </button>
      </div>
    );
  }

  console.log('Current Question:', question);
  console.log('Rendering Question:', question);
  if (showResult && answerResult) {
    try {
      console.log('[Quiz UI] Server result:', {
        serverCorrect: answerResult.serverCorrect,
        serverFeedback: answerResult.serverFeedback,
        serverCorrectOptionId: answerResult.serverCorrectOptionId,
      });
    } catch (_) {}
  }

  /**
   * 🎨 메인 컴포넌트 렌더링
   * 
   * 📱 UI 구조:
   * - 전체 컨테이너 (반응형 스크롤)
   * - 문제 표시 영역
   * - 선택지 목록
   * - 하단 버튼 영역
   * - 학습 모드 오버레이
   */
  return (
    <div
      ref={containerRef}
      className="quiz-question-container"
      style={{ paddingBottom: bottomPad }}
    >
      {/* 상단 진행도 */}
  <ProgressHeader
   current={current + 1}
   total={questions?.length || 1}
   onBack={onBack}
   quizCompletionArr={quizCompletionArr}
    />
      {/* 문제 */}
      <div className="quiz-question-header">
        {(() => {
          const titleText = (
            question?.stemMd ||
            question?.question ||
            question?.prompt ||
            question?.title ||
            question?.text ||
            '문제를 불러오는 중입니다...'
          );
          return (
            <h2 className="quiz-question-title">
              {titleText}
            </h2>
          );
        })()}

        {/* 스토리형 지문: 제목/본문 렌더링 (있을 때만) */}
        {isStoryType && (question?.storyTitleMd || question?.storyBodyMd) && (
          <div className="quiz-question-article-text-block" style={{ background:'#F6FAFF', border:'1px solid #E3F0FF', borderRadius:12, padding:16, marginTop:8 }}>
            {question?.storyTitleMd && (
              <div className="quiz-question-article-title" style={{ color:'#1C3C78' }}>{question.storyTitleMd}</div>
            )}
            {question?.storyBodyMd && (
              <div className="quiz-question-article-body" style={{ whiteSpace:'pre-wrap' }}>{question.storyBodyMd}</div>
            )}
          </div>
        )}

        {/* 기사형 지문: 제목 없이 본문만 렌더링 */}
        {isArticleType && question?.articleBodyMd && (
          <div className="quiz-question-article-text-block">
            <div className="quiz-question-article-body">{question.articleBodyMd}</div>
          </div>
        )}

        {/* 문제와 기사 이미지 사이 안내 문구 (요청 사양/샘플 텍스트는 숨김, 필요 시 서버 제공 문구 사용) */}
        {isArticleType && !question?.articleTitleMd && !question?.articleBodyMd && (
          <div className="quiz-question-article-guide">
            {/* 서버에서 별도 지시문이 오지 않은 경우 가이드 문구는 생략 */}
          </div>
        )}

        {/* 기사 이미지 타입이면 제목 아래에 이미지(또는 플레이스홀더) */}
        {isArticleType && (
          <div className="article-image-wrap">
            {imgSrc && !imgError ? (
              <img
                ref={articleImgWrapperRef}
                src={imgSrc}
                alt="기사 이미지"
                onLoad={handleArticleImgLoad}
                onError={() => {
                  if (q4FallbackIndexRef.current < imgCandidates.length - 1) {
                    q4FallbackIndexRef.current += 1;
                    setImgSrc(imgCandidates[q4FallbackIndexRef.current]);
                  } else {
                    setImgError(true);
                  }
                }}
                className="quiz-question-article-img"
                style={{
                  width: '100%',
                  height: articleImgHeight ?? fallbackHeight ?? 'auto',
                  display: 'block',
                  margin: 0
                }}
              />
            ) : (
              <div
                ref={articleImgWrapperRef}
                className="quiz-question-article-img-placeholder"
                style={{ height: articleImgHeight ?? fallbackHeight ?? 'auto' }}
              >
                <span style={{ fontWeight:600 }}>기사 이미지 영역</span>
                {!imgError ? (
                  <span style={{ opacity:0.7, fontSize:12 }}>업로드 시 비율에 맞춰 이미지 높이에 맞춰 표시됩니다</span>
                ) : (
                  <div style={{ textAlign:'center' }}>
                    <div style={{ fontSize:12, color:'#9AA6B2' }}>이미지를 찾을 수 없습니다.</div>
                    <div style={{ fontSize:12, color:'#9AA6B2' }}>다음 위치에 이미지를 추가하거나 문제에 image URL을 제공하세요:</div>
                    <div style={{ fontSize:12, color:'#4A6FB0', marginTop:4, lineHeight:'18px' }}>
                      src/assets/explore/q4-article.png
                    </div>
                  </div>
                )}
              </div>
            )}
          </div>
        )}

        {question?.options?.map((opt, idx) => {
          const isSelected = selected === idx;
          // 서버 판정이 있으면 우선 적용
          const serverSaysCorrect = showResult && isSelected && typeof answerResult?.serverCorrect === 'boolean' ? answerResult.serverCorrect : null;
          const isCorrect = showResult && isSelected && (serverSaysCorrect === null ? idx === correctIdx : serverSaysCorrect);
          const isWrong = showResult && isSelected && !isCorrect;
          let cardClass = "quiz-question-option-card";
          if (isCorrect) cardClass += " correct";
          else if (isWrong) cardClass += " wrong";
          else if (isSelected) cardClass += " selected";
          let badgeClass = "quiz-question-option-badge";
          if (isCorrect) badgeClass += " correct";
          else if (isWrong) badgeClass += " wrong";
          const badgeLetter = String.fromCharCode(65 + idx);
          return (
            <div
              key={idx}
              onClick={() => handleSelect(idx)}
              className={cardClass}
              style={{ cursor: showResult ? "not-allowed" : (selected === null ? "pointer" : "default") }}
            >
              <div className="quiz-question-option-row">
                <div className={badgeClass}>{badgeLetter}</div>
                <div className="quiz-question-option-text">
                  {opt.contentMd || opt.content || opt.text || opt}
                </div>
              </div>
            </div>
          );
        })}

        {/* 정답/피드백 */}
        {showResult && selected != null && (
          <div className="quiz-question-explanation">
            <div className="quiz-question-explanation-header">
              <span className="quiz-question-explanation-label">{(answerResult?.serverCorrect === false) ? '피드백' : '정답'}</span>
              {Number.isInteger(correctIdx) && correctIdx >= 0 && (
                <div className="quiz-question-explanation-badge">
                  <span className="quiz-question-explanation-badge-text">{String.fromCharCode(65 + correctIdx)}</span>
                </div>
              )}
            </div>
            <div className="quiz-question-explanation-text">
              {(typeof answerResult?.serverCorrect === 'boolean')
                ? (answerResult.serverCorrect
                    ? (answerResult?.serverFeedback || question.answerExplanationMd || question.explanation || "해설이 준비되지 않았습니다.")
                    : (answerResult?.serverFeedback || ""))
                : (selected === correctIdx
                    ? (question.answerExplanationMd || question.explanation || "해설이 준비되지 않았습니다.")
                    : "")}
            </div>
          </div>
        )}

  {!showResult && (
    <div style={{ marginTop: "16px" }}>
      <div
        onClick={() => setShowLearning(!showLearning)}
        className="quiz-question-learn-btn"
      >
        <span className="quiz-question-learn-btn-label">🏫 학습하기</span>
        {showLearning ? (
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none"><path d="M12 16.7498C11.801 16.7508 11.6099 16.6715 11.47 16.5298L3.47003 8.52985C3.19467 8.23434 3.2028 7.77384 3.48841 7.48823C3.77402 7.20261 4.23452 7.19449 4.53003 7.46985L12 14.9398L19.47 7.46985C19.7655 7.19449 20.226 7.20261 20.5117 7.48823C20.7973 7.77384 20.8054 8.23434 20.53 8.52985L12.53 16.5298C12.3901 16.6715 12.1991 16.7508 12 16.7498Z" fill="black"/></svg>
        ) : (
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" style={{ transform: "rotate(180deg)" }}><path d="M12 16.7498C11.801 16.7508 11.6099 16.6715 11.47 16.5298L3.47003 8.52985C3.19467 8.23434 3.2028 7.77384 3.48841 7.48823C3.77402 7.20261 4.23452 7.19449 4.53003 7.46985L12 14.9398L19.47 7.46985C19.7655 7.19449 20.226 7.20261 20.5117 7.48823C20.7973 7.77384 20.8054 8.23434 20.53 8.52985L12.53 16.5298C12.3901 16.6715 12.1991 16.7508 12 16.7498Z" fill="black"/></svg>
        )}
      </div>
      {showLearning && (
        <div className="quiz-question-learning-wrap">
          <div className="quiz-question-learning-svg-wrap">
            {/* Flexible speech bubble */}
            <div className="quiz-question-learning-svg-inner quiz-question-learning-bubble">
              <div className="quiz-question-learning-svg-label">{normalizePlain(question?.hintMd) || '이 문제는 말 그대로 용어의 정의를 묻고 있어요!'}</div>
            </div>
            <div className="quiz-question-learning-ant"> 
              <svg width="72" height="72" viewBox="0 0 72 72" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" style={{ filter:'drop-shadow(0 2px 4px rgba(0,0,0,0.25))' }}>
                <rect width="72" height="72" fill="url(#pattern0_903_5350)" />
                <defs>
                  <pattern id="pattern0_903_5350" patternContentUnits="objectBoundingBox" width="1" height="1"><use xlinkHref="#image0_903_5350" transform="scale(0.00347222)" /></pattern>
                  <image id="image0_903_5350" width="288" height="288" preserveAspectRatio="none" xlinkHref="data:image/png;base64," />
                </defs>
              </svg>
            </div>
          </div>
          <div style={{ marginBottom:1 }} />
          <div className="quiz-question-learning-chalkboard-wrap">
            <div className="quiz-question-learning-chalkboard-inner">
              <div
                ref={chalkTextRef}
                className="quiz-question-learning-chalkboard-text"
              >
                {renderLearningContent()}
              </div>
              {/* 텍스트가 끝난 후 정확히 16px 아래에 분필 라인 */}
              <div className="quiz-question-chalk-line white" />
              <div className="quiz-question-chalk-line red" />
            </div>
            <div className="quiz-question-chalk-bottom-bar" />
          </div>
        </div>
      )}
    </div>
  )}

  {/* 핵심 포인트 */}
  {!showResult && (
    <div className="quiz-question-point-wrap" style={{ marginTop: showLearning ? 16 : 24 }}>
      <div className="quiz-question-point-card">
        <div className="quiz-question-point-title">💡 핵심 포인트</div>
        {(() => {
          // 요구사항: 핵심포인트에는 solvingKeypointsMd가 들어감
          const hintContent = normalizePlain(
            question?.solvingKeypointsMd || question?.keyPointsMd || question?.hintMd || "힌트가 준비되지 않았습니다."
          );
          const renderMdInlineBold = (text) => {
            if (!text || typeof text !== 'string') return null;
            const parts = text.split(/(\*\*.*?\*\*)/g);
            return parts.map((p, i) => {
              const m = /^\*\*(.*?)\*\*$/.exec(p);
              if (m) return <strong key={i}>{m[1]}</strong>;
              return <span key={i}>{p}</span>;
            });
          };
          return (
            <div className="quiz-question-point-content">
              <div className="quiz-question-point-content-pre" style={{ whiteSpace: 'pre-wrap' }}>{renderMdInlineBold(hintContent)}</div>
            </div>
          );
        })()}
        {!showHint && (
          <div className="quiz-question-point-hint-blur">
            <button onClick={() => setShowHint(true)} className="quiz-question-point-hint-btn">
              <svg width="266" height="85" viewBox="0 0 266 85" fill="none" xmlns="http://www.w3.org/2000/svg">
                <rect width="266" height="85" rx="16" fill="white" fillOpacity="0.8" />
                <path d="M132 13.5098C134.023 13.5098 135.671 13.9835 136.971 14.8623C138.27 15.7397 139.104 16.9418 139.636 18.1992C140.325 19.8258 140.542 21.6457 140.612 23.1885H141.169C142.295 23.1886 143.207 24.1013 143.207 25.2266V37.4521C143.206 38.5773 142.295 39.4891 141.169 39.4893H122.831C121.706 39.489 120.794 38.5772 120.793 37.4521V25.2266C120.793 24.1014 121.705 23.1887 122.831 23.1885H123.375C123.449 21.3902 123.69 19.4337 124.47 17.7656C125.038 16.5524 125.902 15.4547 127.195 14.6758C128.478 13.9027 130.074 13.5098 132 13.5098ZM132 16.5664C130.487 16.5664 129.47 16.8733 128.772 17.2939C128.083 17.7088 127.594 18.3002 127.239 19.0605C126.707 20.1984 126.51 21.626 126.438 23.1885H137.55C137.485 21.8833 137.308 20.5396 136.822 19.3906C136.465 18.5472 135.964 17.87 135.26 17.3945C134.559 16.9206 133.537 16.5665 132 16.5664Z" fill="black" />
                <path d="M92.4815 59.598V61.026H86.2095V59.598H88.4355V58.422H90.2555V59.598H92.4815ZM88.2255 63.756C88.2255 64.386 88.6875 64.722 89.3735 64.722C90.0455 64.722 90.5075 64.386 90.5075 63.756C90.5075 63.126 90.0455 62.79 89.3735 62.79C88.6875 62.79 88.2255 63.126 88.2255 63.756ZM92.1455 63.756C92.1455 65.114 90.9975 66.066 89.3735 66.066C87.7355 66.066 86.5875 65.114 86.5875 63.756C86.5875 62.398 87.7355 61.446 89.3735 61.446C90.9975 61.446 92.1455 62.398 92.1455 63.756ZM88.3935 68.446V66.976H97.2695V71.218H95.4215V68.446H88.3935ZM95.5195 58.296H97.2695V66.374H95.5195V63.21H94.5675V66.318H92.8455V58.52H94.5675V61.726H95.5195V58.296ZM106.494 63.7L105.612 65.142C104.156 64.708 103.162 63.826 102.588 62.692C102 63.924 100.95 64.904 99.4384 65.38L98.5424 63.924C100.698 63.252 101.622 61.614 101.622 60.032V58.786H103.498V60.032C103.498 61.586 104.408 63.098 106.494 63.7ZM102.546 67.634V69.608H107.502V67.634H102.546ZM100.726 71.05V66.192H109.308V71.05H100.726ZM107.446 58.31H109.308V65.59H107.446V58.31ZM118.338 60.844V64.498H120.97V60.844H118.338ZM120.578 68.264H125.52V69.748H113.844V68.264H118.73V65.968H114.824V64.498H116.504V60.844H114.754V59.374H124.554V60.844H122.804V64.498H124.512V65.968H120.578V68.264ZM136.873 58.324V67.578H135.011V58.324H136.873ZM128.151 62.412C128.151 63.448 128.907 64.078 129.859 64.078C130.825 64.078 131.553 63.448 131.553 62.412C131.553 61.348 130.825 60.732 129.859 60.732C128.907 60.732 128.151 61.348 128.151 62.412ZM133.359 62.412C133.359 64.288 131.833 65.674 129.859 65.674C127.885 65.674 126.345 64.288 126.345 62.412C126.345 60.508 127.885 59.136 129.859 59.136C131.833 59.136 133.359 60.508 133.359 62.412ZM130.139 69.51H137.195V70.98H128.291V66.668H130.139V69.51ZM148.911 64.96V66.416H139.853V59.248H148.813V60.732H141.729V62.09H148.519V63.518H141.729V64.96H148.911ZM138.481 68.222H150.157V69.72H138.481V68.222ZM156.793 62.566V64.372H162.309V62.566H156.793ZM160.475 68.208H165.403V69.706H153.727V68.208H158.627V65.842H154.959V59.108H156.793V61.11H162.309V59.108H164.129V65.842H160.475V68.208ZM166.76 59.64H172.948C172.948 63.588 171.618 66.808 167.04 69.02L166.074 67.564C169.364 65.982 170.792 63.966 171.058 61.11H166.76V59.64ZM174.922 58.296H176.784V71.19H174.922V58.296Z" fill="black" />
              </svg>
            </button>
          </div>
        )}
      </div>
    </div>
  )}

    </div>

  <div style={{ height: 0 }} />

  {/* 하단 버튼 */}
      <div
        ref={buttonRef}
        className="quiz-question-bottom-btn-wrap"
      >
        <button
          disabled={selected === null}
          onClick={() => {
            if (!showResult) {
              onCheck();
            } else {
              handleNext();
            }
          }}
          className="quiz-question-bottom-btn"
        >
          {showResult ? "다음" : "채점하기"}
        </button>
      </div>
    </div>
  );
}
